<template>
    <div>
        <el-card>
            <el-descriptions class="margin-top" title="题目信息" :column="2" :size="size" border>
                <template #extra>
                    <el-button type="primary" @click="onupdate">修改信息</el-button>
                    <el-button type="success" @click="onSubmit()">发布</el-button>
                </template>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <iphone />
                            </el-icon>
                            题目名称
                        </div>
                    </template>
                    {{ title.name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            创建老师
                        </div>
                    </template>
                    {{ title.create_teacher_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <location />
                            </el-icon>
                            最后修改老师
                        </div>
                    </template>
                    {{ title.update_teacher_name }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            学科
                        </div>
                    </template>
                    {{ title.subject }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            是否将题目公开
                        </div>
                    </template>
                    {{ title.open_type }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            创建时间
                        </div>
                    </template>
                    {{ title.create_time }}
                </el-descriptions-item>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <tickets />
                            </el-icon>
                            修改时间
                        </div>
                    </template>
                    {{ title.update_time }}
                </el-descriptions-item>
            </el-descriptions>
            <el-descriptions direction="vertical" :column="1" :size="size" border>
                <el-descriptions-item>
                    <template #label>
                        <div class="cell-item">
                            <el-icon :style="iconStyle">
                                <office-building />
                            </el-icon>
                            内容
                        </div>
                    </template>
                    <pre>{{ title.content }}</pre>
                </el-descriptions-item>
            </el-descriptions>
        </el-card>
        <el-dialog :modal-append-to-body="false" v-model="operaDialog" title="发布题目" @close="resetForm">
            <el-form status-icon :model="operaForm" ref="operaForm" class="operaDialog" label-width="100px" :rules="rules">
                <el-form-item label="题目名称" prop="name">
                    {{ operaForm.name }}
                </el-form-item>
                <el-form-item label="课程" prop="course_id">
                    <el-select v-model="operaForm.course_id" placeholder="请选择课程" size="large">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
                    </el-select>
                </el-form-item>
                <el-form-item label="答题时间" prop="open_type">
                    <el-date-picker v-model="pushTime" type="datetimerange" range-separator="到" start-placeholder="开始时间"
                        end-placeholder="结束时间" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="publish(operaForm)">提交</el-button>
                    <el-button @click="resetForm">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog :modal-append-to-body="false" v-model="updateDialog" title="修改题目信息" @close="updateresetForm">
            <el-form status-icon :model="updateOperaForm" ref="updateOperaForm" class="updateDialog" label-width="100px" :rules="rules">
                <el-form-item label="题目名称" prop="name">
                    <el-input v-model="updateOperaForm.name" />
                </el-form-item>
                <el-form-item label="学科" prop="subject">
                    <el-input v-model="updateOperaForm.subject" />
                </el-form-item>
                <el-form-item label="是否公开" prop="open_type">
                    <el-switch v-model="updateOperaForm.open_type" />
                </el-form-item>

                <el-form-item label="内容" prop="content">
                    <el-input type="textarea" v-model="updateOperaForm.content" />
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="updateSubmitForm()">提交</el-button>
                    <el-button @click="updateresetForm">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
import moment from "moment";
import { title_details, title_publish, course_find ,title_update} from "@/api/teacher/api.js";
// eslint-disable-next-line no-unused-vars
export default {
    data() {
        return {

            title: {},
            tableData: [],

            pageSize: 10, // 分页的页面大小
            total: 0, // 分页的数据总数
            currentPage: 1, // 分页的当前页
            //查询
            pushTime: [
                "",
                "",
            ],
            options: [],
            updateDialog: false,
            operaDialog: false,
            operaForm: {
                name: "",
                course_id: "",
                title_id: "",
                start_time: "",
                end_time: "",
            },

            updateOperaForm: {
                name: "",
                course_id: "",
                title_id: "",
                start_time: "",
                end_time: "",
            }

        }
    },
    created() {
        this.getUserMessage()
        this.getOptions()
    },
    methods: {
        async getUserMessage() {
            var title_id = this.$route.query.title_id
            title_details({ _id: title_id }).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    console.log(res);
                    this.title = res.data.data

                    console.log(this.title);
                    // this.$router.push('/manager').catch(() => { })
                }
            })
        },
        async getOptions() {
            const params = {
                teacher_id: sessionStorage.getItem("user_id"),
            }
            const res = await course_find(params)
            if (res.data.code === 200) {
                if (res.data.data) {
                    var value = "";
                    var label = "";
                    for (let index = 0; index < res.data.data.data.length; index++) {
                        value = res.data.data.data[index]._id
                        label = res.data.data.data[index].name
                        this.options.push({
                            value: value,
                            label: label,
                        })
                    }
                } else {
                    this.options = []
                }
            }
        },
        onSubmit() {
            this.operaForm.name = this.title.name;
            this.operaForm.title_id = this.title._id;
            this.operaDialog = true;
        },
        publish(data) {
            data.start_time = moment(this.pushTime[0]).format('YYYY-MM-DD HH:mm:ss')
            data.end_time = moment(this.pushTime[1]).format('YYYY-MM-DD HH:mm:ss')
            title_publish(data).then(res => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "success",
                    })
                    this.operaDialog = false
                } else {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "error",
                    })
                }
            })

        },
        resetForm() {

            this.$refs.operaForm.clearValidate()
            this.operaForm = {},
                this.operaDialog = false
        },
        onupdate() {
            this.updateDialog = true
            this.updateOperaForm={...this.title}
        },
        updateresetForm() {
            this.updateDialog = false
            this.updateOperaForm={}
        },
        updateSubmitForm(){
            
            title_update( this.updateOperaForm).then(res => {
                if (res.data.code === 200) {
                    this.$message({
                        showClose: true,
                        message: "修改成功",
                        type: "success",
                    })
                } else {
                    this.$message({
                        showClose: true,
                        message: res.data.msg,
                        type: "error",
                    })
                }
                this.getUserMessage()
                this.updateDialog = false

            })
        },
    },
};
</script>
  
<style lang="less" scoped>
.header {
    margin-top: 20px;
    margin-bottom: 20px;

    .searchBtn {
        float: right;
    }

    .headerForm {
        padding: 0 !important;

        .el-form-item {
            margin-bottom: 0px;
        }
    }
}
</style>
  